import "./conact.scss"
import logo from "./images/contact.svg"

const Conact = ()=>{
    return(
        <section className="input-comment">
            <div className="container">
                <div className="heading text-center">
                    <h2 className="font_main"> </h2>
                    <div className="border"></div>
                    <p className="font_child">       </p>
                </div>
                <div className="content-input-comment">
                    <div className="row">
                        <div className="col-md-6 offset-md-1" data-aos="fade-up" data-aos-easing="ease-in-sine" data-aos-offset="130">
                            <form id="asd">
                                <div className="form-group">
                                    <input className="items " id="user_posts" type="text" placeholder="" required />
                                </div>
                                <div className="form-group">
                                    <input className="items" id="email_posts" type="email" placeholder="" required />
                                </div>
                                <div className="form-group">
                                    <textarea id="messege_posts" name="subject_posts" placeholder=" ......" required></textarea>
                                </div>
                                <div className="form-group text-center">
                                    <input type="submit" className="btn_a btn btn-dark" id="send_comment" value="" />
                                </div>

                            </form>
                        </div>
                        <div className="col-md-5" data-aos="fade-down" data-aos-easing="ease-in-sine" data-aos-offset="130">
                            <img src={logo} alt="...loading" />
                        </div>
                    </div>
                </div>
            </div>
        </section>  
    )
}

export default Conact