import './navbar.scss'

import person from "./image/person.jpg"
import logo from "./image/nav-logo.png"

const Navbar = () => {
  return (
    <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">
        <div className='container'>
            <ul class="navbar-nav">

                <li class="nav-item dropdown no-arrow">
                    <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <img class="img-profile rounded-circle" id="image_housing" src={person} />
                        <span class="mr-2 d-none d-lg-inline text-gray-600 small id_notefcation text-center" id="id_user">
                        <span style={{color: "#28a745"}}> </span>
                        <p style={{margin: "0px"}}></p>
                        </span>
                    </a>
                    <div class="dropdown-menu dropdown-menu-right shadow animated--grow-in afterr" aria-labelledby="userDropdown">
                        <a class="dropdown-item" href="profile/profile.html"> 
                            <i class="fa fa-user fa-sm fa-fw mr-2 text-gray-400"></i>
                              
                        </a>
                        
                        <div class="dropdown-divider"></div>
                        <button class="dropdown-item"  id="btn_log_out">
                            <i class="fa fa-sign-out fa-sm fa-fw mr-2 text-gray-400"></i>
                              
                        </button>
                    </div>
                </li>

                <div class="topbar-divider d-none d-sm-block"></div>

                <li class="nav-item dropdown notification"> 
                    <a class="nav-link nav-icons" href="#" id="navbarDropdownMenuLink1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <i class="fa fa-fw fa-bell"></i> 
                        <span class="indicator"></span>
                    </a>
                    <ul class="dropdown-menu dropdown-menu-right notification-dropdown afterr">
                        <li>
                            <div class="notification-title"> </div>
                            <div class="notification-list">
                                <div class="list-group">
                                    <a href="#" class="list-group-item list-group-item-action active">
                                        <div class="notification-info">
                                            <div class="notification-list-user-img">
                                                <img id="img_notification" src={logo} alt="" class="user-avatar-md rounded-circle" />
                                            </div>
                                            <div class="notification-list-user-block">
                                                <span id="info_notification" class="notification-list-user-name"> </span>         . 
                                                <div id="time_notification" class="notification-date">2022-01-03T18:52:08.782Z</div>
                                            </div>
                                        </div>
                                    </a> 
                                </div>
                                <div class="list-group">
                                    <a href="#" class="list-group-item list-group-item-action active">
                                        <div class="notification-info">
                                            <div class="notification-list-user-img">
                                                <img id="img_notification" src={logo} alt="" class="user-avatar-md rounded-circle" />
                                            </div>
                                            <div class="notification-list-user-block">
                                                <span id="info_notification" class="notification-list-user-name"> </span>         . 
                                                <div id="time_notification" class="notification-date">2022-01-03T18:52:08.782Z</div>
                                            </div>
                                        </div>
                                    </a> 
                                </div>
                                <div class="list-group">
                                    <a href="#" class="list-group-item list-group-item-action active">
                                        <div class="notification-info">
                                            <div class="notification-list-user-img">
                                                <img id="img_notification" src={logo} alt="" class="user-avatar-md rounded-circle" />
                                            </div>
                                            <div class="notification-list-user-block">
                                                <span id="info_notification" class="notification-list-user-name"> </span>         . 
                                                <div id="time_notification" class="notification-date">2022-01-03T18:52:08.782Z</div>
                                            </div>
                                        </div>
                                    </a> 
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="list-footer"> <a href="#">  </a></div>
                        </li>
                    </ul>
                </li>

            </ul>
            <a class="navbar-brand" style={{margin: "auto"}} href="#">
                <img src={logo} width="100" height="50" class="d-inline-block align-top" alt="" />
            </a>

            <button className='btn btn-success' style={{ borderRadius: "20px"}}>
                <i style={{ marginLeft: "5px", background: "#136604", padding: "5px", borderRadius: "50%", fontSize: "12px"}} className='fa fa-plus'></i>
                  
            </button>
        </div>

    </nav>
  );
};

export default Navbar;
