import React  from 'react'

import Content from './contant/content'
import HeadContent from "./head content/headContent"
import SideContent from "./side content/sideContent"


const Home = ()=>{
    return(
        <React.Fragment>
            <HeadContent />
            <div className='main_content'>
                <Content />
                <SideContent />
            </div>
        </React.Fragment>
    )
}

export default Home;